.aboutMe {
    position: relative;
    padding: 150px 0 100px 0;
    border: 2px solid deeppink;
    margin-top: 233px;
}

.commonInf {
    display: flex;
    border: 1px solid black;
    /*padding: 20px 50px 0 50px;*/
    width: 90%;
    margin: 50px auto;
}

.left {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    width: 40%;
    padding: 25px 35px 0 0;

    border: 1px solid #454505;
}

.data {
    margin-bottom: 20px;;
}

.dataInfo {
    font-size: 16px;
    color: #7e878f;
    line-height: 25px;
    margin: 0 0 10px;
}

.dataInfo span {
    display: inline-block;
    width: 75px;
    font-weight: bold;

}


.signature {
    color: #2c3e50;
    font-family: 'Arizonia', cursive;
    font-size: 30px;
    margin: 30px 0;
}

.button {
    width: 80%;
    padding: 15px 25px;

    text-transform: uppercase;
    background-color: #F54F36;
}

.right {
    width: 60%;

    border: 1px solid red;
}

.interest {
    display: flex;
    align-items: center;
    justify-content: center;

    width: 130px;
    height: 130px;
    margin: 5px;

    border-radius: 50%;
    background-color: #f1f1f1;


    color: #7e868e;
    font-size: 16px;
    text-align: center;
    font-weight: 500;
}

.interests h4 {
    font-size: 20px;
    font-weight: bold;

    margin-bottom: 20px;

    text-align: center;
    text-transform: uppercase;

    color: #7e868e;
}

.titles {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    padding: 0 20px;
}

@media screen and (max-width: 1025px) {
    .commonInf {
        width: 100%;
    }

    .left {
        justify-content: flex-start;
        padding-left: 20px;
    }
}


@media screen and (max-width: 768px) {
    .commonInf {
        flex-direction: column;

    }

    .left {
        width: 100%;
        align-items: center;
        padding: 0;
        margin-bottom: 50px;
    }

    .right {
        width: 100%;
        align-items: center;
        padding: 0;
    }
}